body {
  margin: 0 auto;
  background-color: rgba(90, 90, 90,1);
}

.App {
  text-align: center;
}

a {
  text-decoration: none;
}

a:visited {color: black;}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.project-section {
  background: rgb(255,255,255);
  background: linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(90, 90, 90,1) 100%);
}

.container {
  margin: 0 auto;
  max-width: 70rem;
}

/* LANDING PAGE BANNER CSS */
.banner-group {
  background-image: linear-gradient(transparent, white 75%),  url("./img/banner.jpeg");
  background-size: cover;
  min-height: 50rem;
}

.banner {
  display: flex;
  justify-content: flex-end;
  text-align: center;
  flex-direction: column;
  align-items: center;
  padding: 25rem 2rem 5rem;
}

.banner img {
  width: 6rem;
  margin: 0rem;
}

.banner h1 {
  font-size: 3rem;
  margin: 1rem 0rem 0.2rem;
  padding: 1rem;
}

.banner h2 {
  font-size: 1.25rem;
  margin: 0rem 0rem 1rem;
  padding: 1rem;
}

.banner a {
  padding: 0.2rem;
  font-size: 1rem;
}

.banner-links img {
  width: 2rem;
  margin: 1rem;
  opacity: 1;
  transition: opacity 0.4s ease-in-out;
  -webkit-transition: opacity 0.4s ease-in-out;
}

.about-me {
  background-color: white;
  padding: 5rem;
  width: 100%
}

.banner-links img:hover {
  opacity: 0.4;
  transition: opacity 0.4s ease-in-out;
  -webkit-transition: opacity 0.4s ease-in-out;
}

/*NAVBAR CSS*/

.navbar {
  position: absolute;
  display: flex;
  min-height: 100%;
  width: 100%;
}

.navbar-background {
  background-color: rgba(255,255,255,1);
  height: 6rem;
  margin: 10rem;
}

#nav-img {
  justify-content: flex-start;
  flex: 4 1 auto;
  display: flex;
}

#nav-img img {
  width: 4rem;
  margin: 0 1rem;
}

#nav-img a {
  margin: 0;
  padding: 1rem;
  
}

.nav-links {
  list-style-type: none;
  overflow: hidden;
  margin: 0 auto;
  padding: 1rem;
  display: flex;
  width: 100%;
  flex-grow:1;
  box-sizing: border-box;
}

.nav-links li {
  flex: 1 1 auto;
  display: flex;
  justify-content: flex-end;
}

.nav-links li a {
  justify-content: flex-end;
  flex: auto;
  display: block;
  text-align: center;
  padding: 1rem;
  margin: 0.5rem 1rem;
  position: absolute;
}

/* PROJECTS CSS */

.projectprev-box {
  margin: 5rem auto;
  min-width: 40rem;
  max-width: 50rem;
  background-color: white;
  box-shadow: -1px 10px 15px 5px rgb(0 0 0 / 32%);
  border-radius: 10px;
  text-align: center;
  position: relative;
}

.projectprev-box-text {
  display: block;
  overflow: hidden;
  height: 100%;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.projectprev-box-text h3 {
  font-size: 1.9rem;
  font-family: 'Kanit';
  margin: 0 0 4rem 0;
}

.projectprev-box-text p {
  font-size: 1.3rem;
}

.projectprev-img {
 
}

.projectprev-box img {
  width: 100%;
  height: 100%;
  margin: auto;
  object-fit: cover;
  overflow: hidden;
  max-height: 15rem;
  max-width: 15rem;
  min-height: 15rem;
}

.projects-group {
  padding: 10rem 0;
  margin: 0 17rem;
}

.projectprev-title {

}

/* SMALL PROJECTS CSS */

#small-proj-header {
  text-align: center;
  padding: 0 0 10rem 0;
}

#small-proj-header h2 {
  font-size: 3rem;
}

.small-projects-group {
  margin: 0 auto;
  padding: 0 5rem;
  text-align: center;
  display: grid;
  grid-gap: 1rem 1rem;
  grid-template-columns: repeat(auto-fill, minmax(25rem, 1fr));
  grid-auto-rows: 1fr;
}

.small-project-box {
  min-height: 15rem;
  display: flex;
  flex-direction: column;
  text-align: center;
  border-radius: 0.7rem;
  -webkit-box-shadow: -1px 10px 15px 5px rgba(0,0,0,0.32); 
  box-shadow: -1px 10px 15px 5px rgba(0,0,0,0.32);
  padding: 4rem 4rem;
  margin: 1rem;
  background-color: white;
  position: relative;
}

.small-project-box:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0);
  transition: background 1s ease;
  border-radius: 0.7rem;
}

.small-project-box:hover:after {
  background: rgba(0, 0, 0, 0.3);
}

.small-project-items {
  flex: 1 1 auto;
}

.small-project-box h3 {
  font-size: 1.5rem;
}

.small-project-box p {
  font-size: 1.1rem;
}

.small-project-tech {
  font-size: 1rem!important;
  color: #616161;
}

.small-project-img {
  max-width: 2rem;
  align-self: flex-end;
  opacity: 0;
  transition: opacity .5s ease-in-out;
  -webkit-transition: opacity .5s ease-in-out;
}

.small-project-box:hover .small-project-img {
  opacity: 1;
  transition: opacity .75s ease-in-out;
  -webkit-transition: opacity .75s ease-in-out;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media screen and (max-width: 510px) {
  .small-projects-group {
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .small-project-box {
    margin: 0;
    max-width: 60vw;
    width: 100%;
    padding: 2rem;
    border: 1rem white solid;
  }
}

@media screen and (max-width: 800px) {
  .small-project-img {
    max-width: 2rem;
    align-self: flex-end;
    opacity: 1;
  }
}